.info_panel {
  padding-left: 20px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.info_panel .item_li {
  width: 88px;
  margin-right: 20px;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-bottom: 20px;
}
.info_panel .item_li .item_li_img {
  width: 48px;
  height: 48px;
  background: #F2F2F2;
  border-radius: 50%;
  color: var(--theme-color);
  margin-bottom: 6px;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: center;
}
.info_panel .item_li .item_li_img .item_li_img_icon {
  font-size: 25px;
}
.info_panel .item_li .item_li_text {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 400;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: inherit;
}
.info_panel .item_li:hover .item_li_img {
  background: var(--theme-color);
}
.info_panel .item_li:hover .item_li_img .item_li_img_icon {
  color: #fff;
}
.info_panel .item_li:hover .item_li_text {
  color: var(--theme-color);
}
